
import { defineComponent, ref } from "vue";
import type { PFM202001SC01_REQ_EDIT, PFM202001SC01_REQ_REGISTER, PFM202001SC01_REQ_DELETE, PFM202001SC01_RES_DETAIL, PFM201001SC00_REQ_FeeCalcMethodTypeCode_List } from "@/shared/type/PFM202001SC01";
import '@vuepic/vue-datepicker/dist/main.css'
import VueDatePicker from '@vuepic/vue-datepicker';
import { modalController } from "@ionic/vue";
import currencyInput from "@/shared/components/CurrencyInput/CurrencyInput.vue";
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
import { CommonCodeType } from "@/shared/type/commonCodeType";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

enum FeeCalcApplyBaseCode {
    amountFee = '01',
    rateFee = '02',
}

export default defineComponent({
    name: "pfm202001sc01",
    components: {
        VueDatePicker,
        currencyInput,
        ModifyCard,
        BreadCrumb,
        SelectOption
    },
    data() {
        const dataFeeByTransaction = ref<PFM202001SC01_RES_DETAIL>({
            productFeeCode: '',
            feeCurrencyCode: '',
            applyStartDate: new Date(),
            applySeqNo: "",
            applyEndDate: new Date(),
            feeApplyStatusCode: '',
            baseFeeRate: "0",
            baseFeeAmount: "0",
            baseFeeApplyStartAmount: "0",
            feeMinAmount: "0",
            feeMaxAmount: "0",
            minFeeRate: "0",
            maxFeeRate: "0",
            feeCalcApplyBaseCode: "01",
            firstRegisterBranchCode: '',
            firstRegisterTellerID: '',
            firstRegisterTellerName: '',
            firstRegisterDate: "",
            firstRegisterTime: '',
            lastChangeBranchCode: '',
            lastChangeTellerID: '',
            lastChangeTellerName: '',
            lastChangeDate: "",
            lastChangeTime: '',
            deleteBranchCode: "",
            deleteTellerID: "",
            deleteTellerName: "",
            deleteDate: "",
            deleteTime: "",
        });
        return {
            dataFeeByTransaction,
            modifyCard: {
                registerTellerId: '',
                registerTellerName: '',
                registerDate: '',
                registerTime: '',
                updatedTellerId: '',
                updatedTellerName: '',
                updatedDate: '',
                updatedTime: '',
                deleteTellerId: '',
                deleteTellerName: '',
                deleteDate: '',
                deleteTime: ''
            },
            feeCalcMethodTypeCodes: [] as PFM201001SC00_REQ_FeeCalcMethodTypeCode_List[],
            productFeeCode: "" as string,
            isEdit: true as boolean,
            isDisableField: true as boolean,
            isRequiredFiled: false as boolean,
            isInvalidFeeAmount: false as boolean,
            isInvalidFeeRate: false as boolean,
            isInvalidApplyStartDate: false as boolean,
            isInvalidMinAmount: false as boolean,
            isInvalidBaseFeeAmount: false as boolean,
            isInvalidMinFeeRate: false as boolean,
            isInvalidBaseFeeRate: false as boolean,
            isDisableFeeCalcBaseCode: false as boolean,
            isInvalidFeeCalcBaseCode: false as boolean,
            isDisableBaseFeeRate: false as boolean,
            isDisableBaseFeeAmount: false as boolean,
            commonValue: {
                FeeApplyStatusCode: [] as CommonCodeType[],
                FeeCurrencyCode: [] as CommonCodeType[]
            },
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
            productFee: {
                calcCode: '',
                startDate: new Date(),
                endDate: new Date()
            },
        };
    },

    watch: {
        productFeeCode(newVal: string) {
            this.feeCalcMethodTypeCodes.map((item: PFM201001SC00_REQ_FeeCalcMethodTypeCode_List, index) => {
                if (Number(newVal) == index) {
                    this.productFee.startDate = this.$datetime.convertStringToDate(item.applyStartDate)
                    this.productFee.endDate = this.$datetime.convertStringToDate(item.applyEndDate)
                    this.productFee.calcCode = item.feeCalcApplyBaseCode
                    if (this.displayType == 'register') {
                        this.dataFeeByTransaction.productFeeCode = item.productFeeCode;
                        this.dataFeeByTransaction.feeCurrencyCode = item.feeCurrencyCode;
                        this.dataFeeByTransaction.applyStartDate = this.$datetime.convertStringToDate(item.applyStartDate)
                        this.dataFeeByTransaction.applyEndDate = this.$datetime.convertStringToDate(item.applyEndDate)
                    }
                }
            })
        },
        dataFeeByTransaction: {
            handler(newCustomerData) {
                // ===== Fee Calc = Rate =====
                if (newCustomerData.feeCalcApplyBaseCode == '02') {
                    if (Number(newCustomerData.feeMinAmount) > Number(newCustomerData.feeMaxAmount)) {
                        this.isInvalidMinAmount = true;

                    } else {
                        this.isInvalidMinAmount = false;
                    }
                    this.isDisableFeeCalcBaseCode = false;
                    this.isDisableBaseFeeRate = false;
                    this.isDisableBaseFeeAmount = true;
                    this.dataFeeByTransaction.baseFeeAmount = 0;
                } else {
                    // ===== Fee Calc = Amount =====
                    this.isDisableFeeCalcBaseCode = true;
                    this.isDisableBaseFeeRate = true;
                    this.isDisableBaseFeeAmount = false;
                    this.dataFeeByTransaction.baseFeeRate = 0;
                    newCustomerData.feeMaxAmount = newCustomerData.baseFeeAmount;
                    newCustomerData.feeMinAmount = newCustomerData.baseFeeAmount;
                }
            },
            deep: true,
        },
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
        displayType() {
            return this.$route.query.type;
        },
        getProductFeeCode() {
            return this.$route.query.productFeeCode;
        },
        queryRoute() {
            return this.$route.query;
        },
        btnDelete(): boolean {
            if (this.displayType !== 'register' && this.isEdit) {
                return true;
            }
            return false;
        },
        btnCancel(): boolean {
            if (this.displayType == 'register' || this.isEdit == false) {
                return true;
            }
            return false;
        },
        btnList(): boolean {
            if (this.displayType !== 'register' && this.isEdit) {
                return true;
            }
            return false;
        },
        btnEdit(): boolean {
            if (this.displayType !== 'register' && this.isEdit) {
                return true;
            }
            return false;
        },
        formattedStartDate(): string {
            return this.$datetime.formatVueDatePicker(this.dataFeeByTransaction.applyStartDate)
        },
        formattedEndDate(): string {
            return this.$datetime.formatVueDatePicker(this.dataFeeByTransaction.applyEndDate)
        },
        // invalidFeeMinAmount(): boolean {
        //     return Number(this.dataFeeByTransaction.feeMinAmount) > Number(this.dataFeeByTransaction.baseFeeAmount) ? true : false;
        // },
        invalidBaseFeeAmount(): boolean {
            return Number(this.dataFeeByTransaction.baseFeeAmount) > Number(this.dataFeeByTransaction.feeMaxAmount) ? true : false;
        },
        invalidMinFeeRate(): boolean {
            return Number(this.dataFeeByTransaction.minFeeRate) > Number(this.dataFeeByTransaction.baseFeeRate) ? true : false;
        },
        // invalidBaseFeeRate(): boolean {
        //     return Number(this.dataFeeByTransaction.baseFeeRate) > Number(this.dataFeeByTransaction.maxFeeRate) ? true : false;
        // },
        isDisableRate(): boolean {
            return this.productFee.calcCode !== FeeCalcApplyBaseCode.rateFee
        },

        isDisableAmount(): boolean {
            return this.productFee.calcCode !== FeeCalcApplyBaseCode.amountFee
        },
        isInvalidFieldFeeCalcBaseCode(): boolean {
            return this.dataFeeByTransaction.feeCalcApplyBaseCode === '' ? true : false;
        },
        invalidFeeMinAmountByFeeCalc(): boolean {
            return Number(this.dataFeeByTransaction.feeMinAmount) > Number(this.dataFeeByTransaction.feeMaxAmount) ? true : false;
        }
    },

    async created() {
        if (this.approvalType === '') {
            await this.requestFeeCalcMethodTypeCode();
            switch (this.displayType) {
                case "register":
                    this.isDisableField = false;
                    this.dataFeeByTransaction.feeApplyStatusCode = this.$store.state.CommonCodeModule.commonCode.FeeApplyStatusCode[0].key
                    break;
                case 'edit':
                    this.requestFeeByTransactionDetail();
            }
        } else {
            await this.requestFeeCalcMethodTypeCode();
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const body = {
                        productFeeCode: authDetailParsed.productFeeCode,
                        feeCurrencyCode: authDetailParsed.feeCurrencyCode,
                        applyStartDate: authDetailParsed.applyStartDate,
                        applySeqNo: Number(authDetailParsed.applySeqNo)
                    };
                    const res = await this.$request.getRequest('PFM20200112', body, false) as any
                    const data = res.body;
                    const formatValue = {
                        applyStartDate: data.applyStartDate ? this.$datetime.convertStringToDate(data.applyStartDate) : "",
                        applyEndDate: data.applyEndDate ? this.$datetime.convertStringToDate(data.applyEndDate) : "",
                        baseFeeAmount: data.baseFeeAmount ? this.$format.formatCurrency(data.baseFeeAmount, data.feeCurrencyCode) : "",
                        baseFeeApplyStartAmount: data.baseFeeApplyStartAmount ? this.$format.formatCurrency(data.baseFeeApplyStartAmount, data.feeCurrencyCode) : "",
                        feeMaxAmount: data.feeMaxAmount ? this.$format.formatCurrency(data.feeMaxAmount, data.feeCurrencyCode) : "",
                        feeMinAmount: data.feeMinAmount ? this.$format.formatCurrency(data.feeMinAmount, data.feeCurrencyCode) : "",
                    }
                    this.dataFeeByTransaction = { ...res.body, ...formatValue };
                } else {
                    const data = authDetailParsed;
                    const formatValue = {
                        applyStartDate: data.applyStartDate ? this.$datetime.convertStringToDate(data.applyStartDate) : "",
                        applyEndDate: data.applyEndDate ? this.$datetime.convertStringToDate(data.applyEndDate) : "",
                        baseFeeAmount: data.baseFeeAmount ? this.$format.formatCurrency(data.baseFeeAmount, data.feeCurrencyCode) : "",
                        baseFeeApplyStartAmount: data.baseFeeApplyStartAmount ? this.$format.formatCurrency(data.baseFeeApplyStartAmount, data.feeCurrencyCode) : "",
                        feeMaxAmount: data.feeMaxAmount ? this.$format.formatCurrency(data.feeMaxAmount, data.feeCurrencyCode) : "",
                        feeMinAmount: data.feeMinAmount ? this.$format.formatCurrency(data.feeMinAmount, data.feeCurrencyCode) : "",
                    }
                    this.dataFeeByTransaction = { ...authDetailParsed, ...formatValue };
                }
            })
        }
    },

    methods: {
        async requestFeeCalcMethodTypeCode() {
            await this.$request.getRequest('PFM20100113', { feeCalcMethodTypeCode: '01' }, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.feeCalcMethodTypeCodes = res.body.list;
                        if (this.displayType === 'register') {
                            this.dataFeeByTransaction.productFeeCode = res.body.list[0].productFeeCode;
                            this.dataFeeByTransaction.feeCurrencyCode = res.body.list[0].feeCurrencyCode;
                        }
                    }
                });
        },
        async requestFeeByTransactionDetail() {
            const body = {
                productFeeCode: this.queryRoute.productFeeCode,
                feeCurrencyCode: this.queryRoute.feeCurrencyCode,
                applyStartDate: this.queryRoute.applyStartDate,
                applySeqNo: Number(this.queryRoute.applySeqNo)
            };
            const res: any = await this.$request.getRequest('PFM20200112', body, false);
            const data = res?.body;
            const formatValue = {
                applyStartDate: data.applyStartDate ? this.$datetime.convertStringToDate(data.applyStartDate) : "",
                applyEndDate: data.applyEndDate ? this.$datetime.convertStringToDate(data.applyEndDate) : "",
                baseFeeAmount: data.baseFeeAmount ? this.$format.formatCurrency(data.baseFeeAmount, data.feeCurrencyCode) : "",
                baseFeeApplyStartAmount: data.baseFeeApplyStartAmount ? this.$format.formatCurrency(data.baseFeeApplyStartAmount, data.feeCurrencyCode) : "",
                feeMaxAmount: data.feeMaxAmount ? this.$format.formatCurrency(data.feeMaxAmount, data.feeCurrencyCode) : "",
                feeMinAmount: data.feeMinAmount ? this.$format.formatCurrency(data.feeMinAmount, data.feeCurrencyCode) : "",
            }
            this.dataFeeByTransaction = { ...res.body, ...formatValue };
            if (this.dataFeeByTransaction.productFeeCode) {

                this.feeCalcMethodTypeCodes.map((item: PFM201001SC00_REQ_FeeCalcMethodTypeCode_List, index) => {
                    if (item.productFeeCode == this.dataFeeByTransaction.productFeeCode) {
                        this.productFeeCode = index.toString()
                        console.log('item.productFeeCode', item.productFeeCode);
                        console.log(this.dataFeeByTransaction.productFeeCode);


                    }
                })

                this.modifyCard = {
                    registerTellerId: res.body.firstRegisterTellerID,
                    registerTellerName: res.body.firstRegisterTellerName,
                    registerDate: res.body.firstRegisterDate,
                    registerTime: res.body.firstRegisterTime,
                    updatedTellerId: this.dataFeeByTransaction.feeApplyStatusCode === '04' ? '' : res.body.lastChangeTellerID,
                    updatedTellerName: this.dataFeeByTransaction.feeApplyStatusCode === '04' ? '' : res.body.lastChangeTellerName,
                    updatedDate: this.dataFeeByTransaction.feeApplyStatusCode === '04' ? '' : res.body.lastChangeDate,
                    updatedTime: this.dataFeeByTransaction.feeApplyStatusCode === '04' ? '' : res.body.lastChangeTime,
                    deleteTellerId: this.dataFeeByTransaction.feeApplyStatusCode === '04' ? res.body.lastChangeTellerID : '',
                    deleteTellerName: this.dataFeeByTransaction.feeApplyStatusCode === '04' ? res.body.lastChangeTellerName : '',
                    deleteDate: this.dataFeeByTransaction.feeApplyStatusCode === '04' ? res.body.lastChangeDate : '',
                    deleteTime: this.dataFeeByTransaction.feeApplyStatusCode === '04' ? res.body.lastChangeTime : '',
                };
            }
        },


        async handleDeleteRequest() {
            const body: PFM202001SC01_REQ_DELETE = {
                applySeqNo: Number(this.dataFeeByTransaction.applySeqNo),
                applyStartDate: this.$datetime.convertDateToString(this.dataFeeByTransaction.applyStartDate),
                productFeeCode: this.dataFeeByTransaction.productFeeCode,
                feeCurrencyCode: this.dataFeeByTransaction.feeCurrencyCode,
            };
            await this.$request.getRequest('PFM20200141', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.$router.push("PFM202001SC00")
                        this.$dialog.onOpen({
                            content: this.$t("message.delete_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                modalController.dismiss()
                            }
                        });
                    }
                })
        },

        async handleRegisterRequest() {
            const body: PFM202001SC01_REQ_REGISTER = {
                productFeeCode: this.dataFeeByTransaction.productFeeCode,
                feeCurrencyCode: this.dataFeeByTransaction.feeCurrencyCode,
                applyStartDate: this.$datetime.convertDateToString(this.dataFeeByTransaction.applyStartDate),
                applyEndDate: this.$datetime.convertDateToString(this.dataFeeByTransaction.applyEndDate),
                baseFeeRate: Number(this.dataFeeByTransaction.baseFeeRate),
                baseFeeAmount: Number(this.dataFeeByTransaction.baseFeeAmount) ? Number(this.dataFeeByTransaction.baseFeeAmount) : 0,
                baseFeeApplyStartAmount: Number(this.dataFeeByTransaction.baseFeeApplyStartAmount),
                feeMinAmount: Number(this.dataFeeByTransaction.feeMinAmount) ? Number(this.dataFeeByTransaction.feeMinAmount) : 0,
                feeMaxAmount: Number(this.dataFeeByTransaction.feeMaxAmount) ? Number(this.dataFeeByTransaction.feeMaxAmount) : 0,
                minFeeRate: Number(this.dataFeeByTransaction.minFeeRate) ? Number(this.dataFeeByTransaction.minFeeRate) : 0,
                maxFeeRate: Number(this.dataFeeByTransaction.maxFeeRate) ? Number(this.dataFeeByTransaction.maxFeeRate) : 0,
                feeCalcApplyBaseCode: this.dataFeeByTransaction.feeCalcApplyBaseCode
            };
            await this.$request.getRequest('PFM20200121', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.$router.push("PFM202001SC00");
                        this.$dialog.onOpen({
                            content: this.$t("message.register_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                modalController.dismiss()
                            }
                        });
                    }
                });
        },

        async handleEditRequest() {
            const body: PFM202001SC01_REQ_EDIT = {
                productFeeCode: this.dataFeeByTransaction.productFeeCode,
                feeCurrencyCode: this.dataFeeByTransaction.feeCurrencyCode,
                applyStartDate: this.$datetime.convertDateToString(this.dataFeeByTransaction.applyStartDate),
                applySeqNo: Number(this.dataFeeByTransaction.applySeqNo),
                applyEndDate: this.$datetime.convertDateToString(this.dataFeeByTransaction.applyEndDate),
                feeApplyStatusCode: this.dataFeeByTransaction.feeApplyStatusCode,
                baseFeeRate: Number(this.dataFeeByTransaction.baseFeeRate),
                baseFeeAmount: this.dataFeeByTransaction.baseFeeAmount ? Number(this.$format.unformatCurrency(this.dataFeeByTransaction.baseFeeAmount)) : 0,
                baseFeeApplyStartAmount: this.dataFeeByTransaction.baseFeeApplyStartAmount ? Number(this.$format.unformatCurrency(this.dataFeeByTransaction.baseFeeApplyStartAmount)) : 0,
                feeMinAmount: this.dataFeeByTransaction.feeMinAmount ? Number(this.$format.unformatCurrency(this.dataFeeByTransaction.feeMinAmount)) : 0,
                feeMaxAmount: this.dataFeeByTransaction.feeMaxAmount ? Number(this.$format.unformatCurrency(this.dataFeeByTransaction.feeMaxAmount)) : 0,
                minFeeRate: this.dataFeeByTransaction.minFeeRate ? Number(this.dataFeeByTransaction.minFeeRate) : 0,
                maxFeeRate: this.dataFeeByTransaction.maxFeeRate ? Number(this.dataFeeByTransaction.maxFeeRate) : 0,
                feeCalcApplyBaseCode: this.dataFeeByTransaction.feeCalcApplyBaseCode
            };
            await this.$request.getRequest('PFM20200131', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.isEdit = true;
                        this.isDisableField = true;
                        this.isRequiredFiled = false;
                        this.requestFeeByTransactionDetail();
                        this.$dialog.onOpen({
                            content: this.$t("message.update_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                this.requestFeeByTransactionDetail()
                                modalController.dismiss()
                            }
                        });
                    }
                })
        },

        handleSave() {
            switch (this.$route.query.type) {
                case "register":
                    this.handleRegisterRequest();
                    break;
                default:
                    this.handleEditRequest();
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "edit":
                    this.isEdit = false;
                    this.isDisableField = false;
                    break;
                case "list":
                    this.$router.push("PFM202001SC00");
                    break;
                case 'delete':
                    this.$dialog.onOpen({
                        content: this.$t("message.would_you_like_to_delete"),
                        dialogMode: "Question",
                        callback: () => {
                            this.handleDeleteRequest();
                            modalController.dismiss();
                        }
                    })
                    break;
                case 'cancel':
                    if (this.displayType == 'register') {
                        this.$dialog.onOpen({
                            content: this.$t("message.are_you_sure_you_want_to_cancel_registration?"),
                            dialogMode: "Question",
                            callback: () => {
                                modalController.dismiss();
                                this.$router.push("PFM202001SC00");
                            }
                        })
                    } else {
                        this.$dialog.onOpen({
                            content: this.$t("message.cancel_without_saving_changes"),
                            dialogMode: "Question",
                            callback: () => {
                                this.isEdit = true;
                                this.isDisableField = true;
                                this.isRequiredFiled = false;
                                this.requestFeeByTransactionDetail()
                                modalController.dismiss();
                            }
                        })
                    }
                    break;
                case 'save':
                    var { productFeeCode, feeMinAmount, feeMaxAmount, applyStartDate, applyEndDate } = this.dataFeeByTransaction;
                    if (!productFeeCode || (Number(feeMinAmount) > Number(feeMaxAmount)) || (this.$validate.startDateEndDateValidation(applyStartDate, applyEndDate) == false) || this.isInvalidFieldFeeCalcBaseCode) {
                        // this.isInvalidMinFeeRate = true;
                        // this.isInvalidBaseFeeRate = true;
                        this.isRequiredFiled = true;
                        this.isInvalidMinAmount = true;
                        this.isInvalidBaseFeeAmount = true;
                        this.isInvalidApplyStartDate = true;
                        this.isInvalidFeeCalcBaseCode = true;
                        this.$util.scrollToElement('inp_error')
                    } else {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: "Question",
                            callback: () => {
                                modalController.dismiss();
                                this.handleSave();
                            }
                        })
                    }
                    break;
            }
        },

    },
});
